<template>
  <div class="withdrawal">
    <div class="withdrawal_head">
      <div class="withdrawal_head_title">
        <i class="icon_arrow_left" @click="goBack"></i>
        <p class="title1">立即提现</p>
        <p class="title2" @click="toIncomeDetails">福利值明细</p>
      </div>
      <div class="withdrawal_head_text">
        <p>可提福利值</p>
        <p class="text2">{{ userLuckyValue.amount }}</p>
        <p>{{ userLuckyValue.exchangeRatio + userLuckyValue.exchangeUnit }}=1元</p>
      </div>
    </div>
    <div class="withdrawal_main">
      <div class="withdrawal_mode">
        <p class="withdrawal_mode_title">提现方式</p>
        <div class="withdrawal_mode_box">
          <p class="mode">
            <i class="icon_wechat"></i>
            <span>微信</span>
          </p>
          <p class="info">
            <img src="../static/img/img_wechat_head.png" alt="">
            <span>光芒熄灭了</span>
          </p>
        </div>
      </div>
      <div class="withdrawal_amount">
        <ul class="withdrawal_amount_list">
        	<li :class="item.id == nowEx?'act':''" v-for="(item,index) in exchangeLists" :key="item.id" @click="getExId(item)">
            <p>{{ item.amount+'元' }}</p>
          </li>
        </ul>
        <p class="btn_withdrawal_a" @click="fnWithdrawal">提 现</p>
      </div>
      <p class="explain_title">提现说明</p>
      <p class="explain_text">1. 满1元即可兑换提现</p>
      <p class="explain_text">2.提现预计5个工作日左右到账</p>
    </div>
  </div>
</template>

<script>
  export default {
    name:'withdrawal',
    data(){
      return {
        nowEx:0,
		obj:{},
        exchangeLists: [],
		userLuckyValue:{}
      }
    },
	mounted() {
		var jsonObj = localStorage.getItem("obj");
		this.obj = JSON.parse(jsonObj);
		this.getMediaExchangeConf();
		this.getUserLuckyValue();
	},
    methods:{
	  goBack(){
		  this.$router.go(-1);
	  },
	  toIncomeDetails(){
		  var _this = this;
		  _this.$router.push({path:'/incomeDetails'});
	  },
      getExId(data){
        this.nowEx = data.id;
		this.amount = data.amount;
      },
	  getMediaExchangeConf(){
		  let _this = this;
		  var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
		  _this.$axios({
		    method: 'post',
		    url: '/getMediaExchangeConf',
		    data:data,
		  }).then(function(res) {
		    console.log('getMediaExchangeConf=========',res);
		    if(res.data.code === 200){
				var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
				console.log(reData);
				_this.exchangeLists = reData.exchangeLists;
		    }else{
		  	
		    }
		  });
	  },
	  fnWithdrawal(){
		  let _this = this;
		  var exclusiveObj = {
		  	amount:_this.amount,
		  	code:'skjhdfkabdfkjsbsfksbkjfbjkbf',
			id:_this.nowEx,
		  };
		  var requestData = Object.assign(_this.obj,exclusiveObj);
		  var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
		  _this.$axios({
		    method: 'post',
		    url: '/withdrawal',
		    data:data,
		  }).then(function(res) {
		    console.log('withdrawal=========',res);
		    if(res.data.code === 200){
				var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
				console.log(reData);
				
		    }else{
		  	
		    }
		  });
	  },
	  getUserLuckyValue(){
	  	let _this = this;
	  	var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(_this.obj));
	  	_this.$axios({
	  	  method: 'post',
	  	  url: '/getUserLuckyValue',
	  	  data:data,
	  	}).then(function(res) {
	  	  if(res.data.code === 200){
	  		var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
	  		_this.userLuckyValue = reData;
	  	  }
	  	});
	  },
    }
  }
</script>

